import React from 'react'
import wa from '../assets/btn-whatsapp.png'

// {/* Botão Whatsapp */}
const BtnWhatsapp = () => {
    return (
        < div className='bg-white shadow-2xl rounded px-5 py-2 flex flex-col items-center mt-6 sm:mt-0 sm:flex-row' >
            <img src={wa} alt='Whatsapp' />
            <div className='ml-2'>
                <h3 className='font-bold text-2xl'> Orce seu Projeto! </h3>
                <p className='text-xs'> Orçamento sem compromisso </p>
                <p className='font-bold'> (21) 9 9999-9999 </p>
            </div>
        </div >
    )
}

export default BtnWhatsapp