// Formatar a identação do documento: Shift + Alt + F
import React from 'react'
import './styles.css'
import BtnWhatsapp from '../components/BtnWhatsapp'
import Logo from '../components/Logo'
import { Helmet } from 'react-helmet'

import img from '../assets/img.png'
import selo from '../assets/selo.png'
import projeto1 from '../assets/projeto1.png'
import projeto2 from '../assets/projeto2.png'
import projeto3 from '../assets/projeto3.png'
import projeto4 from '../assets/projeto4.png'
import projeto5 from '../assets/projeto5.png'
import projeto6 from '../assets/projeto6.png'

import { graphql, useStaticQuery } from 'gatsby'

// CORES DE FUNDO E OUTRAS CONFIGURAÇÕES:  tailwindcss.com/docs/background-color
// p-4 é de padding, que são as bordas de uma caixa, por exemplo
// Class é uma palavra reservada em JavaScript, então utiliza-se ClassName programando em React
// flex é o recurso para organizar o layout. Em dispositivos móveis o flex é em coluna, por isso flex-col
// items-center alinha os itens no centro
// px-5 py-2 são os parágrafos nos eixos x e y
// mt-4 é a marge do box (onde estão os itens do botão dp Whatsapp)
// sm:flex-row faz com que a partir da tela small (640px) o comportamento do flex mude para "em linha" ao invés de coluna (A página do Tailwind explica mais sobre Responsive Design)
// ml-2 é margem left
// justify-between espaça os elementos entre si sem deixar margens entre os elementos das extremidades e os cantos da página
// justify-around espaça os elementos entre si deixando margens entre os elementos das extremidades e os cantos da página
// my-4 é a margem no eixo y
// flex-wrap, no celular ou no computador, faz com que as imagens (nesse caso) ao baterem no máximo da tela "caiam" pra baixo, pra próxima linha. Por isso que no celular onde a tela é menor, as imagens tendem a ficar tudo em coluna. Em quanto que no computador elas vão se ajustar à tela.
// max-w-sm é o máximo de largura da tela small. Dessa forma, se for maior que a tela small, os elementos ficarão um do lado do outro e não um embaixo do outro
// React Helmet adiciona título à página no navegador. Se tiver mais de uma página (cada arquivo na página pages é uma página diferente), é possível adicionar um título para cada página, modificando cada arquivo
// No curso, é usado import Helmet from "react-helmet", mas dá erro. Então foi usado import {Helmet} from "react-helmet", conforme encontrado no site: https://github.com/gatsbyjs/gatsby/issues/15479
// OBS: Os dados são buscados por meio de Hooks, como o useStaticQuery.
// OBS: graphql é uma query. O símbolo `` chama uma string.

// {/* Header ou Cabeçalho */ }
const Header = ({ children }) => { // O structure assignment ({}) com a propriedade especial "children" permite colocar dentro de um componente outros componentes
    return (
        <div className='p-4 bg-indigo-800 sm:flex justify-between items-center'>
            {children}
        </div>
    )
}

// {/* Footer ou Rodapé */ }
const Footer = () => {
    return (
        <div className='p-4 bg-gray-200 sm:flex justify-between items-center'>
            <div>
                <h5> Teste Engenharia </h5>
                <p> Rua tal, bairro y </p>
            </div>
            <BtnWhatsapp />
        </div>
    )
}

// {/* Hero é a primeira parte do site depois do cabeçalho */ }
const Hero = () => {
    return (
        <div className='flex flex-col items-center sm:flex-row sm: justify-between'>
            <div className='p-8'>
                <h2 className='font-bold text-2xl'> Máquinas e equipamentos </h2>
                <p className='text-xl'> Agrícolas e industriais </p>
                <p className='text gray 700'> Atendemos Rio de Janeiro e região</p>
            </div>
            <img src={img} alt='Carreta agrícola' />
        </div>
    )
}



const Index = () => {

    const { site } = useStaticQuery(graphql`
        query MyQuery {
            site {
                siteMetadata {
                    title
                }
            }
        }
    `)

    const selos = [1, 2, 3, 4] // Para não precisar repetir os selos várias vezes, cria-se um vetor 

    return (

        // Utilizando essa estrutura é possível ver que esta parte do site é composta por um cabeçalho (Header) no qual estão um Logo e um Botão
        <div>
            {/* Helmet é o TÍTULO DA PÁGINA NO NAVEGADOR */}
            <Helmet>
                <title>{site.siteMetadata.title}</title>
            </Helmet>
            {/* Header ou Cabeçalho */}
            <Header>
                {/* Logo */}
                <Logo />
                <div>
                    {/* Botão Whatsapp */}
                    <BtnWhatsapp />
                </div>
            </Header>

            {/* Hero é a primeira parte do site depois do cabeçalho */}
            <Hero />

            <div className='flex flex-col items-center sm:flex-row justify-around'>
                {
                    // A função map é utilizada para repetições. Nesse caso, ele vai converter um vetor de 4 itens em 4 imagens
                    selos.map(() => {
                        return <img alt='img selo' src={selo} className='my-4' />
                        // A tag alt foi adiciona apenas porque estava aparecendo um alerta (WARNING) no Terminal. Porém não estava interferindo na compilação da página. O texto inserido 'img selo' pode ser qualquer coisa.
                        // alt é um texto alternado que aparece no lugar da imagem caso dê algum erro na hora de mostrar ela.
                    })
                }

            </div>

            <div>
                <h2 className='px-8 py-2 text-2xl font-bold'>
                    Projetos executados
                </h2>
                <div className='flex flex-wrap justify-around'>


                    <div className='max-w-sm'>
                        <div className='m-2 rounded shadow-lg'>
                            <img src={projeto1} alt='Projeto 1' />
                            <div className='px-6 py-4'>
                                <p className='font-bold tex-xl mb-2'>Projeto</p>
                                <p>Descrição</p>
                            </div>
                        </div>
                    </div>

                    <div className='max-w-sm'>
                        <div className='m-2 rounded shadow-lg'>
                            <img src={projeto2} alt='Projeto 2' />
                            <div className='px-6 py-4'>
                                <p className='font-bold tex-xl mb-2'>Projeto</p>
                                <p>Descrição</p>
                            </div>
                        </div>
                    </div>

                    <div className='max-w-sm'>
                        <div className='m-2 rounded shadow-lg'>
                            <img src={projeto3} alt='Projeto 3' />
                            <div className='px-6 py-4'>
                                <p className='font-bold tex-xl mb-2'>Projeto</p>
                                <p>Descrição</p>
                            </div>
                        </div>
                    </div>

                    <div className='max-w-sm'>
                        <div className='m-2 rounded shadow-lg'>
                            <img src={projeto4} alt='Projeto 4' />
                            <div className='px-6 py-4'>
                                <p className='font-bold tex-xl mb-2'>Projeto</p>
                                <p>Descrição</p>
                            </div>
                        </div>
                    </div>

                    <div className='max-w-sm'>
                        <div className='m-2 rounded shadow-lg'>
                            <img src={projeto5} alt='Projeto 5' />
                            <div className='px-6 py-4'>
                                <p className='font-bold tex-xl mb-2'>Projeto</p>
                                <p>Descrição</p>
                            </div>
                        </div>
                    </div>

                    <div className='max-w-sm'>
                        <div className='m-2 rounded shadow-lg'>
                            <img src={projeto6} alt='Projeto 6' />
                            <div className='px-6 py-4'>
                                <p className='font-bold tex-xl mb-2'>Projeto</p>
                                <p>Descrição</p>
                            </div>
                        </div>
                    </div>

                </div>
            </div>

            {/* Footer ou Rodapé */}
            <Footer />

        </div >
    )
}
export default Index